import {memo, ReactNode, useState} from 'react';
import {classNames, Mods} from "shared/lib/classNames/classNames";
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import {Link} from "react-router-dom";


import cls from "./NavBar.module.scss"
import {Button} from "react-bootstrap";
import {ModalWindow} from "../../shared/ui/ModalWindow/ModalWindow";
import useAuth from "../../shared/hooks/useAuth";

interface NavBarProps {
    className?: string
    children?: ReactNode
}


export const NavBarCustom = memo((props: NavBarProps) => {

    const {
        className,
        children,
        ...otherProps
    } = props
    const { isAuthenticated, login, logout } = useAuth();
    const [showModal, setShowModal] = useState(false);

    const handleClose = () => (
        setShowModal(false),
        login()
    );
    const handleShow = () => setShowModal(true);

    const mods: Mods = {};

    return (
        <div
            className={classNames(cls.NavBarCustom, mods, [className])}
            {...otherProps}
        >
            <Navbar collapseOnSelect expand="lg" className="bg-body-tertiary">
                <Container className={cls.Container}>
                    <Navbar.Brand className={cls.Brand} href="#home">WB RADAR</Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="me-auto">
                            <Link className={cls.Link} to="/"></Link>
                            {isAuthenticated &&
                            <Link className={cls.Link} to="/dashboard"></Link>
                            }
                        </Nav>
                    </Navbar.Collapse>
                    <Navbar.Collapse className="justify-content-end">
                        {isAuthenticated? <Navbar.Text>
                            <a onClick={logout} >{localStorage.getItem('isauth')}</a>
                        </Navbar.Text>
                            :
                        <Button variant="primary" onClick={handleShow}>
                            
                        </Button>}
                    </Navbar.Collapse>
                </Container>
                <ModalWindow show={showModal} handleClose={handleClose}/>
            </Navbar>
            {children}
        </div>
    );
});