import React, {useRef, useEffect} from 'react';
import Chart from 'chart.js/auto';
import {saleChartType} from "entity/DataSetType";

type LineChartProps = {
    labels: string[];
    typeChart: 'line' | 'bar' | 'radar'
    positionChart?: 'x' | "y"
    label: string
    datasets :saleChartType[]
}


const LineChart: React.FC<LineChartProps> = ({
     labels,
     typeChart,
     positionChart = "y",
    datasets
 }) => {
    const chartRef = useRef<HTMLCanvasElement>(null);

    useEffect(() => {
        if (chartRef.current) {
            const ctx = chartRef.current.getContext('2d');
            if (ctx) {
                new Chart(ctx, {
                    type: typeChart,
                    data: {
                        labels: labels,
                        datasets: datasets
                    },
                    options: {
                        indexAxis: positionChart,
                        responsive: true,
                        maintainAspectRatio: false
                    }
                });
            }
        }
    }, [ typeChart, positionChart, datasets]);

    return <canvas ref={chartRef} style={{width: '300px', height: '300px'}}/>;
};

export default LineChart;
